<template>
  <div class="tab-model">
    <div class="tab-button-div">
      <BasicForm @register="formRegister" @advanced-change="advancedChange" />
    </div>

    <div id="maintenance-assess-table-tab-content" class="tab-table-div tab-table-content">
      <a-tabs v-model:activeKey="activeKey" @change="handleTabsChange">
        <a-tab-pane v-if="useUserStore().isMultiOrganization" key="1" tab="统计报表">
          <BasicTable @register="registerTable" @resize-column="(w, col) => (col.width = w)">
            <template #toolbar>
              <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">{{
                t('common.action.export')
              }}</a-button>
            </template>
          </BasicTable>
        </a-tab-pane>
        <a-tab-pane key="2" tab="电站明细" force-render>
          <BasicTable @register="registerStationTable" @resize-column="(w, col) => (col.width = w)">
            <template #toolbar>
              <a-button
                class="yellow-btn"
                :preIcon="IconEnum.EXPORT"
                @click="handleStationExport"
                >{{ t('common.action.export') }}</a-button
              >
            </template>
          </BasicTable>
        </a-tab-pane>
      </a-tabs>
    </div>
  </div>
</template>
<script lang="ts" setup>
  import { onMounted, ref, reactive } from 'vue';
  import { useMessage } from '/@/hooks/web/useMessage';
  import { useI18n } from '/@/hooks/web/useI18n';
  import { IconEnum } from '/@/enums/appEnum';
  import { BasicForm, useForm } from '@/components/Form';
  import { BasicTable, useTable } from '/@/components/Table';
  import { exportExcelFile } from '/@/utils/file/download';
  import { urlToPath } from '/@/utils/route';
  import { organColumns, stationColumns, getFormSearchSchemas, getList, exportExcel } from './data';
  import dayjs from 'dayjs';
  import { useUserStore } from '/@/store/modules/user';

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();
  const stationParams = reactive({ organId: null });
  const { params: routeQuery } = urlToPath();
  const activeKey = ref('1');
  if (!useUserStore().isMultiOrganization || routeQuery.dimension === 'station') {
    activeKey.value = '2';
  }

  const [formRegister, { resetFields, setFieldsValue, getFieldsValue }] = useForm({
    labelWidth: 120,
    baseColProps: { span: 8 },
    actionColOptions: {
      span: 24,
    },
    autoAdvancedLine: 1,
    schemas: getFormSearchSchemas(stationParams),
    compact: true,
    showAdvancedButton: true,
    resetButtonOptions: {
      onClick: async () => {
        await resetFields();
        stationParams.organId = null;
        reload();
        reloadStation();
      },
    },
    submitFunc: () => {
      reload();
      reloadStation();
      return Promise.resolve();
    },
  });

  const getParam = (isStation: boolean) => {
    const query = { ...getFieldsValue() };
    query.isStation = isStation;
    if (query.stationIds && query.stationIds.length > 0) {
      query.stationId = query.stationIds.join(',');
    }
    let param = {} as any;
    for (const key in query) {
      if (key === 'stationIds') {
        continue;
      }
      param[key] = query[key];
    }
    if (param.startMonth) {
      param.startMonth = dayjs(param.startMonth).format('YYYY-MM');
    }
    if (param.endMonth) {
      param.endMonth = dayjs(param.endMonth).format('YYYY-MM');
    }
    return param as any;
  };

  const getListByParam = async (isStation: boolean) => {
    const query = getParam(isStation);
    return await getList(query);
  };

  const [registerTable, { reload, redoHeight }] = useTable({
    api: () => getListByParam(false),
    target: '#maintenance-assess-table-tab-content',
    targetTabKey: '1',
    targetTabValue: activeKey,
    columns: organColumns,
    useSearchForm: false,
    bordered: true,
    pagination: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
  });

  const [registerStationTable, { reload: reloadStation, redoHeight: redoHeightStation }] = useTable(
    {
      api: () => getListByParam(true),
      target: '#maintenance-assess-table-tab-content',
      targetTabKey: '2',
      targetTabValue: activeKey,
      columns: stationColumns,
      useSearchForm: false,
      bordered: true,
      pagination: false,
      showTableSetting: false,
      tableSetting: { fullScreen: true },
      showIndexColumn: true,
      afterFetch: function (data) {
        const count = {};
        for (let index = 0; index < data.length; index++) {
          const element = data[index];
          if (count[element.stationOrganId]) {
            count[element.stationOrganId]++;
            continue;
          }
          count[element.stationOrganId] = 1;
        }
        for (let index = 0; index < data.length; index++) {
          const element = data[index];
          for (const key in count) {
            if (element.stationOrganId === key) {
              element.rowSpan = count[key];
              count[key] = 0;
            }
          }
        }
        return data;
      },
    },
  );

  function handleExport() {
    exportCommonExcel(false, '统计报表.xlsx', organColumns);
  }

  function handleStationExport() {
    exportCommonExcel(true, '电站明细.xlsx', stationColumns);
  }

  function exportCommonExcel(isStation: boolean, filename: string, excelList: any[]) {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = getParam(isStation);
        query.filename = filename;
        query.excelList = excelList;
        await exportExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        query.filename = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  const handleTabsChange = (activeKey) => {
    if (activeKey === '1') {
      reload();
    } else if (activeKey === '2') {
      reloadStation();
    }
  };

  const advancedChange = () => {
    if (activeKey.value === '1') {
      redoHeight();
    } else if (activeKey.value === '2') {
      redoHeightStation();
    }
  };

  onMounted(() => {
    const params = { ...routeQuery };
    if (params.startDate) {
      params.startMonth = routeQuery.startDate;
    }
    if (params.endDate) {
      params.endMonth = routeQuery.endDate;
    }
    if (params.stationIds) {
      params.stationIds = params.stationIds.split(',');
    } else {
      params.stationIds = [];
    }
    setFieldsValue(params);
  });
</script>
<style lang="less" scoped>
  @import '/@/design/biz/bizList.less';
</style>
