export enum Api {
  LIST_URL = '/bi/maintenance/assess/list',
  EXPORT_EXCEL_URL = '/bi/maintenance/assess/export-excel',
  LIST_ORG_URL = '/bi/common/org/info',
}

// 获得细则统计列表
export const getList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 导出细则统计列表
export const exportExcel = (params) =>
  defHttp.post(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
      responseType: 'blob',
    },
    { isOnlyResult: true },
  );

// 获得组织列表
export const getOrgList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_ORG_URL,
      params,
    },
    { isOnlyResult: true },
  );

