export const getFormSearchSchemas = (stationParams): FormSchema[] => {
  return [
    {
      field: '[startMonth, endMonth]',
      label: '考核月份',
      component: 'RangePicker',
      componentProps: {
        format: 'YYYY-MM',
        valueFormat: 'YYYY-MM',
        picker: 'month',
        placeholder: ['开始月份', '结束月份'],
        style: 'width: 100%',
      },
    },
    {
      field: 'stationOrganId',
      label: '所属公司',
      component: 'ApiSelect',
      componentProps: ({ formModel }) => {
        return {
          api: async () => {
            const data = await getOrgList({});
            const options: any = [];
            if (data && data.length > 0) {
              data.forEach((item) => {
                options.push({ value: item.id, label: item.label });
              });
            }
            return options;
          },
          onChange: (value) => {
            stationParams.organId = value;
            formModel.stationIds = [];
          },
        };
      },
      show: useUserStore().isMultiOrganization,
    },
    {
      field: 'stationIds',
      label: '电站名称',
      component: 'ApiSelect',
      componentProps: {
        api: async () => {
          const data = await CommonApi.getStationList({ ...stationParams });
          const options: any = [];
          if (data && data.length > 0) {
            data.forEach((item) => {
              options.push({ value: item.id, label: item.name });
            });
          }
          return options;
        },
        mode: 'multiple',
        maxTagCount: 'responsive',
        filterOption: (input: string, option: any) => {
          return option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0;
        },
      },
    },
  ];
};
